"use client"
import Link from "next/link";
import React from "react";
import { supabase } from "~/lib/supabase";
import {
  Table,
  TableBody,
  TableCaption,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "~/components/ui/table"
import dynamic from "next/dynamic";

const GyroTable = dynamic(() => import("~/components/ui/gyroscopetable"), { ssr: false });

export default async function HomePage() {
  const { data: sigma, error } = await supabase
      .from('sigma')
      .select('*');

  return (
    <main className=" bg-gray-900 flex min-h-screen flex-col items-center justify-center text-white">
      <Table>
        <TableHeader>
          <TableRow>
            <TableHead className="w-[100px]">ID</TableHead>
            <TableHead>Name</TableHead>
            <TableHead>Rizz Level</TableHead>
          </TableRow>
        </TableHeader>
        <TableBody>
        {sigma?.map((sigma) => (
            <TableRow key={sigma.id}>
              <TableCell className="font-medium">{sigma.id}</TableCell>
              <TableCell>{sigma.name}</TableCell>
              <TableCell>{sigma.rizz}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
      <GyroTable />
    </main>
  );
}
